

export const localAPI = {
    getData: async (name) => {
        let myStorage;
        let data = null

        try {

            myStorage = window.localStorage;
            data = myStorage.getItem(name); //   -  localStorage

            return data

        } catch (e) {

            //    (, -  localStorage), 
            //         
            console.log("     localStorage   .     .   .");
            return data
        }

    },
    getParsedData: async (name) => {
        let myStorage;
        let data = null

        try {

            myStorage = window.localStorage;
            data = myStorage.getItem(name); //   -  localStorage
            const parsedData = JSON.parse(data)
            return parsedData

        } catch (e) {

            //    (, -  localStorage), 
            //         
            console.log("     localStorage   .     .   .");
            return data
        }

    },

    setData: async (data, name) => {
        try {
            let myStorage;
            let jsnData = JSON.stringify({ ...data })
            myStorage = window.localStorage;
            myStorage.setItem(name, jsnData);
            let jsnupdtdData = myStorage.getItem(name);
            let updtdData = JSON.parse(jsnupdtdData)

            return updtdData

        } catch (e) {
            //    (, -  localStorage), 
            //         
            console.log("     localStorage   .     .    ");
            return null
        }

    },

    setReportData: async (data, name) => {
        try {
            let myStorage;
            let jsnData = JSON.stringify(data)
            myStorage = window.localStorage;
            myStorage.setItem(name, jsnData);
            let jsnupdtdData = myStorage.getItem(name);
            let updtdData = JSON.parse(jsnupdtdData)

            return updtdData

        } catch (e) {
            //    (, -  localStorage), 
            //         
            console.log("     localStorage   .     .    ");
            return null
        }
    }

}