import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'
import { SIBlock } from '../type/document-infoblock-type';
// import { IPost } from '../models/IPost'


export const infoblockAPI = createApi({
    reducerPath: 'infoblockAPI',
    baseQuery: fetchBaseQuery({
        baseUrl: 'https://april-online.ru/api',
        prepareHeaders: (headers, { getState }) => {
            headers.set('X-Requested-With', 'XMLHttpRequest');
            //  ,    , ,  
            return headers;
        }
    }),

    endpoints: (build) => ({
        fetchAllInfoblocks: build.query<SIBlock[], number>({
            query: (arg: number) => {
                
                return {
                    url: '/infoblocks',
                    // params: {
                    //     _limit: arg
                    // }
                }
            },
            transformResponse: (response: any, meta, arg) => {
                //  resultCode,      0,  
                if (response.resultCode !== 0) {
                    throw new Error(response.message || 'An unexpected error occurred');
                }
                //  ,    
                return response.data;
            },
        }),
        
    }),
    


})

export const { useFetchAllInfoblocksQuery } = infoblockAPI;
